import React, { useRef, useState } from "react";
import { useDispatch } from "react-redux";
import { styles } from "../styles/common";
import { formatError } from "../utils/errors";
import { importEntries } from "../store/entriesSlice";

export default function CsvImportButton({ onNotify, variant = "default" }) {
  const dispatch = useDispatch();
  const inputRef = useRef(null);
  const [loading, setLoading] = useState(false);

  const handleClick = () => {
    inputRef.current?.click();
  };

  const resetInput = () => {
    if (inputRef.current) {
      inputRef.current.value = "";
    }
  };

  const validateFile = (file) => {
    // Guard against accidentally uploading wrong/empty files before hitting the API.
    if (!file) {
      return false;
    }

    const name = file.name?.toLowerCase() || "";
    if (!name.endsWith(".csv")) {
      onNotify?.("Please select a CSV file (.csv).", "error");
      return false;
    }

    if (file.size === 0) {
      onNotify?.("Selected CSV file is empty.", "error");
      return false;
    }

    return true;
  };

  const handleChange = async (event) => {
    const file = event.target.files?.[0];
    if (!file) return;

    if (!validateFile(file)) {
      resetInput();
      return;
    }

    setLoading(true);
    try {
      const result = await dispatch(importEntries(file)).unwrap();
      const summary = result?.summary;
      const created = summary?.created ?? 0;
      const updated = summary?.updated ?? 0;
      const skipped = summary?.skipped ?? 0;
      onNotify?.(
        `CSV import completed (${created} created, ${updated} updated, ${skipped} skipped)`,
        "success"
      );
    } catch (err) {
      onNotify?.(`CSV import failed: ${formatError(err)}`, "error");
    } finally {
      setLoading(false);
      resetInput();
    }
  };

  return (
    <>
      <input
        type="file"
        accept=".csv,text/csv"
        ref={inputRef}
        onChange={handleChange}
        style={{ display: "none" }}
      />
      <button
        type="button"
        onClick={handleClick}
        style={{
          ...styles.button,
          opacity: loading ? 0.7 : 1,
          backgroundColor: variant === "import" ? "#29442f" : styles.button.backgroundColor,
          borderColor: variant === "import" ? "#29442f" : styles.button.backgroundColor,
        }}
        disabled={loading}
      >
        {loading ? "Importing..." : "Import CSV"}
      </button>
    </>
  );
}
